import React from 'react';
import MapComponent from "../components/MapComponent";
import styled from "styled-components";

const StyledContainer = styled.div`
    font-family: "MaruBuri";
    font-weight: 400;
    text-align: center;
    padding-bottom: 10%;
`;


export default function MapContainer() {
    return (
        <div>
            <StyledContainer>
                찾아오시는 길
            </StyledContainer>
            <MapComponent/>
        </div>
    );
}